.nav {
  line-height: 1;
  font-weight: 900;
  margin-top: -#{get-size('300')};
  margin-inline-start: -#{get-size('300')};

  &__list {
    display: flex;
    flex-wrap: wrap;

    > * {
      padding-top: get-size('100');
      padding-inline-start: get-size('300');
    }
  }

  a {
    text-decoration: none;
    display: block;
    position: relative;

    // Adds the fake border to active state items
    &[data-state='active'],
    &[aria-current='page'] {
      &:before {
        content: '';
        display: block;
        width: 100%;
        height: 0.25rem;
        position: absolute;
        top: 100%;
        left: 0;
        margin-top: 0.25rem;
        background: get-color('primary-shade');
      }
    }
  }
}